<template>
  <div id="app">
    <input type="file" id="file" placeholder="请选择文件">
    <el-button type="primary" size="small" @click="handleSubmit">确定</el-button>
    <div class="table-wrapper">
      <el-table v-if="dataList" :data="dataList" style="width: 100%" height="90vh">
        <el-table-column prop="id" label="编号" min-width="100px" fixed>
        </el-table-column>
        <el-table-column prop="name" label="姓名" min-width="100px" fixed>
        </el-table-column>
        <el-table-column label="日期">
          <el-table-column label="星期">
          </el-table-column>
        </el-table-column>
        <el-table-column v-for="(item, index) in monthDate" :key="index" :label="String(item.day)" prop="data">
          <el-table-column :label="item.weekCn.replace('星期','')" prop="data">
            <template slot-scope="scope">
              <el-tooltip effect="dark" placement="top-start">
                <div slot="content" v-html="dateFilter(scope.row.data[index].data)"></div>
                <el-tag v-if="scope.row.data[index].status == 'k'" type="danger">{{scope.row.data[index].statusCn}}</el-tag>
                <el-tag v-else-if="scope.row.data[index].status == 'q'" type="info">{{scope.row.data[index].statusCn}}</el-tag>
                <el-tag v-else-if="scope.row.data[index].status == 'c'" type="warning">{{scope.row.data[index].statusCn}}</el-tag>
                <el-tag v-else-if="scope.row.data[index].status == 'z'" type="warning">{{scope.row.data[index].statusCn}}</el-tag>
                <el-tag v-else-if="scope.row.data[index].status == 'cz'" type="warning">{{scope.row.data[index].statusCn}}</el-tag>
                <span v-else-if="scope.row.data[index].status == 'n'">{{scope.row.data[index].statusCn}}</span>
                <el-tag v-else-if="scope.row.data[index].status == 'j'">{{scope.row.data[index].statusCn}}</el-tag>
                <span v-else>-</span>
              </el-tooltip>
              <!-- <template v-else>
              <el-tag v-if="scope.row.data[index].status == 'k'" type="danger">旷工</el-tag>
              <el-tag v-else-if="scope.row.data[index].status == 'c'" type="warning">迟到</el-tag>
              <el-tag v-else-if="scope.row.data[index].status == 'z'" type="info">早退</el-tag>
              <span v-else-if="scope.row.data[index].status == 'n' && scope.row.data[index].week != 0 && scope.row.data[index].week != 6" style="background-color:white">正常</span>
            </template> -->
            </template>
          </el-table-column>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
const XLSX = require('xlsx')
let $ = document.querySelector.bind(document)
// 获取一个月有多少天
function getDaysInOneMonth(year, month) {
  month = parseInt(month, 10)
  let d = new Date(year, month, 0)
  return d.getDate()
}

// 获取一个月中每天的日期和星期
function getMonthData(year, month) {
  let dayCount = getDaysInOneMonth(year, month)
  let date = new Date(year, month - 1, 1)
  const weekMap = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六']
  let ret = []
  for (let index = 1; index <= dayCount; index++) {
    date.setDate(index)
    ret.push({
      week: date.getDay(),
      weekCn: weekMap[date.getDay()],
      day: index
    })
  }
  return ret
}
export default {
  name: 'landing-page',
  data() {
    return {
      monthDate: null,
      dataList: null
    }
  },
  // filters: {
  //   dateFilter(val) {
  //     if (val && val.join) {
  //       return val.map(item => item['考勤时间']).join('<br>')
  //     }
  //     return ''
  //   }
  // },
  methods: {
    dateFilter(val) {
      if (val && val.join) {
        return val.map(item => item['考勤时间']).join('<br>')
      }
      return ''
    },
    handleSubmit() {
      if (!$('#file').files[0]) {
        this.$message({
          message: '要先选择xlsx文件',
          type: 'warning'
        })
        return
      }
      let files = $('#file').files
      var fileReader = new FileReader()
      fileReader.onload = (ev) => {
        try {
          var data = ev.target.result

          var workbook = XLSX.read(data, {
            type: 'binary'
          })
          // 以二进制流方式读取得到整份excel表格对象

          var persons = [] // 存储获取到的数据
        } catch (e) {
          console.log('文件类型不正确')
          return
        }

        // 表格的表格范围，可用于判断表头是否数量是否正确
        var fromTo = ''
        // 遍历每张表读取
        for (var sheet in workbook.Sheets) {
          if (workbook.Sheets.hasOwnProperty(sheet)) {
            fromTo = workbook.Sheets[sheet]['!ref']
            console.log('fromTo', fromTo)
            persons = persons.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]))
            // break; // 如果只取第一张表，就取消注释这行
          }
        }

        console.log(persons)
        console.log('====time====', persons[0]['考勤时间'])
        let date = new Date(persons[0]['考勤时间'])
        let monthDate = getMonthData(date.getFullYear(), date.getMonth())
        this.monthDate = monthDate
        let group = {}
        persons.forEach(item => {
          item.date = new Date(item['考勤时间'])
          if (!group[item['人员编号']]) {
            group[item['人员编号']] = []
          }
          group[item['人员编号']].push(item)
        })
        let ret = []
        Object.keys(group).forEach(personKey => {
          let person = group[personKey]
          let result = {
            id: personKey,
            name: group[personKey][0]['姓名']
          }
          result.data = monthDate.map(dayItem => {
            let target = person.filter(personDay => {
              return personDay.date.getDate() == dayItem.day
            })
            let itemData
            if (!target || !target.length) {
              if (dayItem.week == 0 || dayItem.week == 6) {
                itemData = {
                  status: 'n',
                  statusCn: ''
                }
              } else {
                itemData = {
                  status: 'k',
                  statusCn: '旷工'
                }
              }
            } else {
              target.sort((a, b) => {
                return a.date - b.date
              })
              if (dayItem.week == 0 || dayItem.week == 6) {
                itemData = {
                  status: 'j',
                  statusCn: '加班',
                  data: [...target]
                }
              } else {
                let beLate = false
                let leaveEarly = false
                let lack = false
                if (target.length < 2) {
                  lack = true
                }
                if (target[0].date.getHours() > 8 || (target[0].date.getHours() == 8 && target[0].date.getMinutes() > 30)) {
                  beLate = true
                }
                if (target[target.length - 1].date.getHours < 18) {
                  leaveEarly = true
                }
                if (lack) {
                  itemData = {
                    status: 'q',
                    statusCn: '缺卡',
                    data: [...target]
                  }
                } else if (beLate && leaveEarly) {
                  console.log('====迟到+早退====', target)
                  itemData = {
                    status: 'cz',
                    statusCn: '迟到+早退',
                    data: [...target]
                  }
                } else if (beLate) {
                  itemData = {
                    status: 'c',
                    statusCn: '迟到',
                    data: [...target]
                  }
                } else if (leaveEarly) {
                  itemData = {
                    status: 'z',
                    statusCn: '早退',
                    data: [...target]
                  }
                } else {
                  itemData = {
                    status: 'n',
                    statusCn: '正常',
                    data: [...target]
                  }
                }
              }
            }
            itemData.week = dayItem.week
            return itemData
          })
          ret.push(result)
        })
        console.log('====group====', group)
        console.log('====ret====', ret)
        this.dataList = ret
      }

      // 以二进制方式打开文件
      fileReader.readAsBinaryString(files[0])

      // let filePath = $('#file').files[0].path
      // let xlsxData = XLSX.readFile(filePath)
      // console.log('====xlsxData====', xlsxData)
      // let worksheet
      // xlsxData.SheetNames.forEach(name => {
      //   worksheet = xlsxData.Sheets[name]
      //   let json = XLSX.utils.sheet_to_json(worksheet)
      //   console.log('====json====', json)

      // })
      // console.log('====xlsxData====', xlsxData)
    }
  }
}
</script>

